import React from 'react';
import Chart from 'chart.js';
import PropTypes from 'prop-types';

class BarChart extends React.Component {
  componentDidMount() {
    let theDatasets;
    if(this.props.datasets) {
      theDatasets = this.props.datasets;
    } else {
      theDatasets = [{
          label: this.props.datasetLabel,
          data: this.props.data,
          backgroundColor: this.props.backgroundColor,
          borderColor: this.props.borderColor,
          borderWidth: this.props.borderWidth,
          xAxisID: this.props.xAxisId,
          yAxisID: this.props.yAxisId,
          borderSkipped: this.props.bordersSkipped,
          hoverBackgroundColor: this.props.hoverBackgroundColor,
          hoverBorderColor: this.props.hoverBorderColor,
          hoverBorderWidth: this.props.hoverBorderWidth
      }];
    }
    let data = {
      labels: this.props.labels,
      datasets: theDatasets
    }
    let options = this.props.options;
    let ctx = document.getElementById(this.props.id).getContext("2d");
    let bar = new Chart(ctx, {
        type: this.props.horizontal ? 'horizontalBar' : 'bar',
        data: data,
        options: options
    });
  }
  render() {
    return (
      <canvas id={this.props.id} width={this.props.width} height={this.props.height}></canvas>
    );
  }
}

BarChart.propTypes = {
  id: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  datasets: PropTypes.array,
  labels: PropTypes.array,
  datasetLabel: PropTypes.string,
  data: PropTypes.array,
  horizontal: PropTypes.bool,
  backgroundColor: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  borderColor: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  borderWidth: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.number
  ]),
  xAxisID: PropTypes.string,
  YAxisID: PropTypes.string,
  borderSkipped: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  hoverBackgroundColor: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  hoverBorderColor: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  hoverBorderWidth: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.array
  ]),
  options: PropTypes.object
};

BarChart.defaultProps = {
  id: 'bar-chart-0',
  width: '300',
  height: '200',
  horizontal: false,
  options: {
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    },
    legend: {
      display: false,
        labels: {
          display: false
        }
    }
  }
};

export default BarChart;
